lor: $color-grey-medium;\r\n\r\n\r\n// Placeholder text colour\r\n//$placeholder-text: $color-grey;\r\n\r\n// Button text color\r\n//$btn-text-color: #fff;\r\n//$btn-text-color-alt: $color-grey-dark;\r\n\r\n// Breakpoints\r\n$mobile-landscape: 30rem; // 480px\r\n$tablet: 40rem; // 640px\r\n$tablet-wide: 48rem; // 768px\r\n$desktop: 64rem; // 1024px\r\n$widescreen: 71.25rem; // 1140px\r\n$hd-widescreen: 100rem; // 1140px\r\n$ultra-hd-widescreen: 134.375rem; // 2150px for Microsoft Surface Pro 3\r\n\r\n// Line height\r\n$line-height-base: 1.5;\r\n\r\n// Transitions\r\n$transition-default-property: all;\r\n$transition-default-easing: ease-in-out;\r\n$transition-default-speed: 0.2s;\r\n$transition-default-speed-easing: $transition-default-speed $transition-default-easing;\r\n\r\n\r\n//// Layout Conditions\r\n$sidebarEnabled: true;\r\n$contentContainerWidth: if($sidebarEnabled, 21, 24); // if($condition, true, false)\r\n$contentContainerPosition: if($sidebarEnabled, 4, 1);\r\n\r\n$sidebarWidth: if($sidebarEnabled, 3, 1); // 3 cols if true, 1 col if false. if false, sidebar will be display:none\r\n$sidebarPosition: if($sidebarEnabled, 1, 1);\r\n\r\n//$showSidebar: if($sidebarEnabled, visible, hidden);\r\n$showSidebar: if($sidebarEnabled, block, none);\r\n\n// /* Mixins.scss */\r\n// e.g. @include respond-min($tablet) {\r\n//        background-color: $color-primary;\r\n//      }\r\n// Docs - http://outlinecss.co.uk/get-started.html#mixins\r\n//---------------------------------------------//\r\n//  * NOTE: Do not include anything that will be actually rendered in CSS, because it will be added to every imported SASS file.\r\n//  * Do not use SASS @import inside resources files. Add imported files directly in resources array in webpack config instead.\r\n//  * See: https://github.com/shakacode/sass-resources-loader\r\n\r\n// ====================================================\r\n// ============== VENDOR LIBRARIES  ===================\r\n// ===================================