tps://github.com/shakacode/sass-resources-loader\r\n\r\n\r\n// html\r\n$html-font-base: 22px; //  used for calculating media query breakpoints\r\n//$font-base: 1rem; //  used for for scaling font-size rhythm - in outline.css framework\r\n$bootstrap-font-base: 22px; // used for scaling bootstrap components - in bootstrap/variables.scss\r\n\r\n$spacing: 1.5rem; // 24px\r\n//$body-background-color: #F0F0F0;\r\n\r\n// Colours\r\n$colors-primary-green: #009688;\r\n$colors-app-background: #2a4f91;\r\n\r\n// Fonts\r\n//$font-primary: 'Segoe UI', san-serif; // body font\r\n//$font-secondary: 'Lato', sans-serif; // title font\r\n//$font-monospace: Monaco, Consolas, 'Lucida Console', monospace;\r\n//$font-base-color: $color-grey-medium;\r\n\r\n\r\n// Placeholder text colour\r\n//$placeholder-text: $color-grey;\r\n\r\n// Button text color\r\n//$btn-text-color: #fff;\r\n//$btn-text-color-alt: $color-grey-dark;\r\n\r\n// Breakpoints\r\n$mobile-landscape: 30rem; // 480px\r\n$tablet: 40rem; // 640px\r\n$tablet-wide: 48rem; // 768px\r\n$desktop: 64rem; // 1024px\r\n$widescreen: 71.25rem; // 1140px\r\n$hd-widescreen: 100rem; // 1140px\r\n$ultra-hd-widescreen: 134.375rem; // 2150px for Microsoft Surface Pro 3\r\n\r\n// Line height\r\n$line-height-base: 1.5;\r\n\r\n// Transitions\r\n$transition-default-property: all;\r\n$transition-default-easing: ease-in-out;\r\n$transition-default-speed: 0.2s;\r\n$transition-default-speed-easing: $transition-default-speed $transition-default-easing;\r\n\r\n\r\n//// Layout Conditions\r\n$sidebarEnabled: true;\r\n$contentContainerWidth: if($sidebarEnabled, 21, 24); // if($condition, true, false)\r\n$contentContainerPosition: if($sidebarEnabled, 4, 1);\r\n\r\n$sidebarWidth: if($sidebarEnabled, 3, 1); // 3 cols if true, 1 col if false. if false, sidebar will be display:none\r\n$sidebarPosition: if($sidebarEnabled, 1, 1);\r\n\r\n//$showSidebar: if($sidebarEnabled, visible, hidden);\r\n$showSidebar: if($sidebarEnabled, block, none);\r\n\n// /* Mixins.scss */\r\n// e.g. @include respond-min($tablet) {\r\n//        background-color: $color-primary;\r\n//      }\r\n// Docs - http://outlinecss.co.uk/get-started.html#mixins\r\n//---------------------------------------------//\r\n//  * NOTE: Do not include anything that will be actually rendered in CSS, because it will be added to every imported SASS file.\r\n//  * Do not use SASS @import inside resources files. Add imported files directly in resources array in webpack config instead.\r\n//  * See: https://github.com/shakacode/sass-resources-loader\r\n\r\n// ====================================================\r\n// ============== VENDOR LIBRARIES  ===================\r\n// ====================================================\r\n//  * We are using \"modular\" components, there is not a single master .scss file, but rather one for each component.\r\n//  * Therefore, each component's .scss file must have all vendor libraries imported into them or they cannot use the mixins\r\n//  * and variables\r\n\r\n\r\n// Susy - SASS Math Library\r\n@import \"~susy/sass/_susy\";\r\n//  Bourbon - SASS Mixins Library\r\n@import \"~bourbon/core/_bourbon.scss\";\r\n\r\n// ====================================================\r\n// ============== OUTLINE.CSS MIXINS ==================\r\n// ====================================================\r\n\r\n// media queries / breakpoints\r\n@mixin respond-min($width) {\r\n  @media screen and (min-width: $width) {\r\n    @content;\r\n  }\r\n}\r\n\r\n// ghost buttons\r\n@mixin ghost-btn($color) {\r\n  color: $color;\r\n  border: .125rem solid $color;\r\n  background-color: transparent;\r\n\r\n  &:hover,\r\n  &:focus,\r\n  &:active {\r\n    background-color: $color;\r\n  }\r\n}\r\n\r\n// gradients\r\n@mixin gradient($angle, $colour1, $colour2) {\r\n  background: $colour1;\r\n  background: linear-gradient(#{$angle}deg, $colour1 0%, $colour2 100%);\r\n  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='$colour1', endColorstr='$colour2', GradientType=1);\r\n}\r\n\r\n// icons\r\n@mixin icon($icon) {\r\n  @extend %icon;\r\n  @extend %icon-#{$icon};\r\n}\r\n\r\n@mixin icon-before($icon, $padding: .5rem) {\r\n  &:before {\r\n    @extend %icon;\r\n    @extend %icon-#{$icon};\r\n    padding-right: $padding;\r\n  }\r\n}\r\n\r\n// ====================================================\r\n// ============== CUSTOM MIXINS =======================\r\n// ====================================================\r\n\r\n@mixin content-padding {\r\n  padding: 10px 20px;\r\n}\r\n\r\n// Transition Mixin\r\n@mixin transition-default($property-speed-ease) {\r\n  // If no args are passed, animate all with $transition-default-speed-easing\r\n  @if $property-speed-ease == 'default' {\r\n    @include transition($transition-default-property $transition-default-speed-easing);\r\n  }\r\n  // If 1 args are passed, animate given property with $transition-default-speed-easing\r\n  @if length($property-speed-ease) == 1 and $property-speed-ease != 'default' {\r\n    @include transition($property-speed-ease $transition-default-speed-easing);\r\n  }\r\n  // If 2 args are passed, animate given property and given transition and default easing\r\n  @if length($property-speed-ease) == 2 {\r\n    @include transition($property-speed-ease $transition-default-easing);\r\n  }\r\n  // If 3 args are passed, animate given property and given transition and given easing\r\n  @if length($property-speed-ease) == 3 {\r\n    @include transition($property-speed-ease);\r\n  }\r\n}\r\n\r\n// border radius\r\n@mixin border-radius($radius) {\r\n  -webkit-border-radius: $radius;\r\n  -moz-border-radius: $radius;\r\n  -ms-border-radius: $radius;\r\n  border-radius: $radius;\r\n}\r\n\n$susyDebug:show;\n$lime:#C0FF33;\n$magenta:#cc33dd;\n/********************/\r\n/* App.scss */\r\n/********************/\r\n/// <reference path=\"../../../node_modules/susy/sass/susy/language/_susy.scss\" />\r\n/// <reference path=\"../../../node_modules/bourbon/app/assets/stylesheets/bourbon\" />\r\n/// <reference path=\"../../assets/styles/shared/variables.scss\" />\r\n/// <reference path=\"../../assets/styles/shared/mixins.scss\" />\r\n/* ====================================================\r\n * ============== PAGE LAYOUT =========================\r\n * ==================================================== */\r\n//@import \"../../assets/styles/global/susy-config\";\r\n/* ====================================================\r\n * ============== APP-WIDE STYLES  ====================\r\n * ==================================================== */\r\n@import '../../assets/styles/appwide-metastyles.scss';\r\n@import '../../assets/styles/global';\r\n\r\n/* ====================================================\r\n * ============== \"APP\" COMPONENT STYLES  =============\r\n * ==================================================== */\r\n\r\nbody {\r\n   &::before {\r\n      content: ' ';\r\n      height: 100%;\r\n      position: fixed; // instead of background-attachment\r\n      width: 100%;\r\n      top: 0;\r\n      left: 0;\r\n      padding-top: 0;\r\n      padding-bottom: 0;\r\n      background-color: $colors-app-background;\r\n      background-image: url('/assets/images/login-ellipse.png');\r\n      background-size: cover;\r\n      background-repeat: no-repeat;\r\n      will-change: transform; // creates a new paint layer\r\n      z-index: -1;\r\n   }\r\n}\r\n\r\n// The Susy Span Mixin Syntax\r\n// @include span( <$span> of [<$context>] [<$last>] );\r\n// @include span( isolate <$span> at [<$column>] [<$last>] );\r\n\r\n/********************/\r\n/* Logged in styles */\r\n/********************/\r\n\r\n//@include nested(24) {\r\n\r\nbody.authenticated {\r\n   &::before {\r\n      background-color: #eeeeee;\r\n      background-image: none;\r\n   }\r\n\r\n   .app-container {\r\n      position: relative;\r\n      top: 0;\r\n      @include clearfix;\r\n\r\n      nav-header {\r\n         width: 100%\r\n      }\r\n\r\n      nav-sidebar { // inputs come from variables.scss\r\n         @include span(isolate $sidebarWidth at $sidebarPosition); // 3 cols if true, 1 col if false.\r\n         display: $showSidebar;\r\n      }\r\n\r\n      .app-state {\r\n         color: unset;\r\n      }\r\n   }\r\n\r\n   .content-container {\r\n      @include span(isolate $contentContainerWidth at $contentContainerPosition last);\r\n      padding: 40px;\r\n   }\r\n\r\n   .content {\r\n      height: unset;\r\n   }\r\n}\r\n\r\n// end body.authenticated\r\n\r\n//}\r\n\r\n// end nested(24)\r\n\r\n/********************/\r\n/* Logged out styles */\r\n/********************/\r\n\r\n@include nested(24) {\r\n   &:not(body.authenticated) {\r\n      .app-container {\r\n         position: relative;\r\n         top: 140px;\r\n         @include clearfix;\r\n\r\n         nav-header {\r\n            width: 100%\r\n         }\r\n\r\n         nav-sidebar {\r\n            display: none;\r\n         }\r\n\r\n         .app-state {\r\n            color: #ffffff;\r\n         }\r\n      }\r\n\r\n      .content-container {\r\n         @include span(24 last);\r\n      }\r\n\r\n      .content {\r\n         //height: 100vh; // This forces .app-container to 100% of your viewport\r\n      }\r\n   }\r\n}\r\n\r\n// end nested(24)\r\n","/* global.scss */\r\n/* Import Materialize CSS Mixins and Variables */\r\n@import \"~materialize-css/sass/components/_mixins\";\r\n@import \"~materialize-css/sass/components/_color\";\r\n@import '~materialize-css/sass/components/_variables';\r\n/* Import custom global styles */\r\n@import \"global/susy-grid-config\";\r\n@import \"global/reset\";\r\n@import \"global/forms\";\r\n@import \"global/alignments\";\r\n@import \"global/display\";\r\n@import \"global/buttons\";\r\n\r\n","// Utility Color Classes\n\n//.success {\n//\n//}\n\n// Google Color Palette defined: http://www.google.com/design/spec/style/color.html\n\n\n$materialize-red: (\n  \"base\":       #e51c23,\n  \"lighten-5\":  #fdeaeb,\n  \"lighten-4\":  #f8c1c3,\n  \"lighten-3\":  #f3989b,\n  \"lighten-2\":  #ee6e73,\n  \"lighten-1\":  #ea454b,\n  \"darken-1\":   #d0181e,\n  \"darken-2\":   #b9151b,\n  \"darken-3\":   #a21318,\n  \"darken-4\":   #8b1014,\n);\n\n$red: (\n  \"base\":       #F44336,\n  \"lighten-5\":  #FFEBEE,\n  \"lighten-4\":  #FFCDD2,\n  \"lighten-3\":  #EF9A9A,\n  \"lighten-2\":  #E57373,\n  \"lighten-1\":  #EF5350,\n  \"darken-1\":   #E53935,\n  \"darken-2\":   #D32F2F,\n  \"darken-3\":   #C62828,\n  \"darken-4\":   #B71C1C,\n  \"accent-1\":    #FF8A80,\n  \"accent-2\":    #FF5252,\n  \"accent-3\":    #FF1744,\n  \"accent-4\":    #D50000\n);\n\n$pink: (\n  \"base\":       #e91e63,\n  \"lighten-5\":  #fce4ec,\n  \"lighten-4\":  #f8bbd0,\n  \"lighten-3\":  #f48fb1,\n  \"lighten-2\":  #f06292,\n  \"lighten-1\":  #ec407a,\n  \"darken-1\":   #d81b60,\n  \"darken-2\":   #c2185b,\n  \"darken-3\":   #ad1457,\n  \"darken-4\":   #880e4f,\n  \"accent-1\":    #ff80ab,\n  \"accent-2\":    #ff4081,\n  \"accent-3\":    #f50057,\n  \"accent-4\":    #c51162\n);\n\n$purple: (\n  \"base\":       #9c27b0,\n  \"lighten-5\":  #f3e5f5,\n  \"lighten-4\":  #e1bee7,\n  \"lighten-3\":  #ce93d8,\n  \"lighten-2\":  #ba68c8,\n  \"lighten-1\":  #ab47bc,\n  \"darken-1\":   #8e24aa,\n  \"darken-2\":   #7b1fa2,\n  \"darken-3\":   #6a1b9a,\n  \"darken-4\":   #4a148c,\n  \"accent-1\":    #ea80fc,\n  \"accent-2\":    #e040fb,\n  \"accent-3\":    #d500f9,\n  \"accent-4\":    #aa00ff\n);\n\n$deep-purple: (\n  \"base\":       #673ab7,\n  \"lighten-5\":  #ede7f6,\n  \"lighten-4\":  #d1c4e9,\n  \"lighten-3\":  #b39ddb,\n  \"lighten-2\":  #9575cd,\n  \"lighten-1\":  #7e57c2,\n  \"darken-1\":   #5e35b1,\n  \"darken-2\":   #512da8,\n  \"darken-3\":   #4527a0,\n  \"darken-4\":   #311b92,\n  \"accent-1\":    #b388ff,\n  \"accent-2\":    #7c4dff,\n  \"accent-3\":    #651fff,\n  \"accent-4\":    #6200ea\n);\n\n$indigo: (\n  \"base\":       #3f51b5,\n  \"lighten-5\":  #e8eaf6,\n  \"lighten-4\":  #c5cae9,\n  \"lighten-3\":  #9fa8da,\n  \"lighten-2\":  #7986cb,\n  \"lighten-1\":  #5c6bc0,\n  \"darken-1\":   #3949ab,\n  \"darken-2\":   #303f9f,\n  \"darken-3\":   #283593,\n  \"darken-4\":   #1a237e,\n  \"accent-1\":    #8c9eff,\n  \"accent-2\":    #536dfe,\n  \"accent-3\":    #3d5afe,\n  \"accent-4\":    #304ffe\n);\n\n$blue: (\n  \"base\":       #2196F3,\n  \"lighten-5\":  #E3F2FD,\n  \"lighten-4\":  #BBDEFB,\n  \"lighten-3\":  #90CAF9,\n  \"lighten-2\":  #64B5F6,\n  \"lighten-1\":  #42A5F5,\n  \"darken-1\":   #1E88E5,\n  \"darken-2\":   #1976D2,\n  \"darken-3\":   #1565C0,\n  \"darken-4\":   #0D47A1,\n  \"accent-1\":    #82B1FF,\n  \"accent-2\":    #448AFF,\n  \"accent-3\":    #2979FF,\n  \"accent-4\":    #2962FF\n);\n\n$light-blue: (\n  \"base\":       #03a9f4,\n  \"lighten-5\":  #e1f5fe,\n  \"lighten-4\":  #b3e5fc,\n  \"lighten-3\":  #81d4fa,\n  \"lighten-2\":  #4fc3f7,\n  \"lighten-1\":  #29b6f6,\n  \"darken-1\":   #039be5,\n  \"darken-2\":   #0288d1,\n  \"darken-3\":   #0277bd,\n  \"darken-4\":   #01579b,\n  \"accent-1\":    #80d8ff,\n  \"accent-2\":    #40c4ff,\n  \"accent-3\":    #00b0ff,\n  \"accent-4\":    #0091ea\n);\n\n$cyan: (\n  \"base\":       #00bcd4,\n  \"lighten-5\":  #e0f7fa,\n  \"lighten-4\":  #b2ebf2,\n  \"lighten-3\":  #80deea,\n  \"lighten-2\":  #4dd0e1,\n  \"lighten-1\":  #26c6da,\n  \"darken-1\":   #00acc1,\n  \"darken-2\":   #0097a7,\n  \"darken-3\":   #00838f,\n  \"darken-4\":   #006064,\n  \"accent-1\":    #84ffff,\n  \"accent-2\":    #18ffff,\n  \"accent-3\":    #00e5ff,\n  \"accent-4\":    #00b8d4\n);\n\n$teal: (\n  \"base\":       #009688,\n  \"lighten-5\":  #e0f2f1,\n  \"lighten-4\":  #b2dfdb,\n  \"lighten-3\":  #80cbc4,\n  \"lighten-2\":  #4db6ac,\n  \"lighten-1\":  #26a69a,\n  \"darken-1\":   #00897b,\n  \"darken-2\":   #00796b,\n  \"darken-3\":   #00695c,\n  \"darken-4\":   #004d40,\n  \"accent-1\":    #a7ffeb,\n  \"accent-2\":    #64ffda,\n  \"accent-3\":    #1de9b6,\n  \"accent-4\":    #00bfa5\n);\n\n$green: (\n  \"base\":       #4CAF50,\n  \"lighten-5\":  #E8F5E9,\n  \"lighten-4\":  #C8E6C9,\n  \"lighten-3\":  #A5D6A7,\n  \"lighten-2\":  #81C784,\n  \"lighten-1\":  #66BB6A,\n  \"darken-1\":   #43A047,\n  \"darken-2\":   #388E3C,\n  \"darken-3\":   #2E7D32,\n  \"darken-4\":   #1B5E20,\n  \"accent-1\":    #B9F6CA,\n  \"accent-2\":    #69F0AE,\n  \"accent-3\":    #00E676,\n  \"accent-4\":    #00C853\n);\n\n$light-green: (\n  \"base\":       #8bc34a,\n  \"lighten-5\":  #f1f8e9,\n  \"lighten-4\":  #dcedc8,\n  \"lighten-3\":  #c5e1a5,\n  \"lighten-2\":  #aed581,\n  \"lighten-1\":  #9ccc65,\n  \"darken-1\":   #7cb342,\n  \"darken-2\":   #689f38,\n  \"darken-3\":   #558b2f,\n  \"darken-4\":   #33691e,\n  \"accent-1\":    #ccff90,\n  \"accent-2\":    #b2ff59,\n  \"accent-3\":    #76ff03,\n  \"accent-4\":    #64dd17\n);\n\n$lime: (\n  \"base\":       #cddc39,\n  \"lighten-5\":  #f9fbe7,\n  \"lighten-4\":  #f0f4c3,\n  \"lighten-3\":  #e6ee9c,\n  \"lighten-2\":  #dce775,\n  \"lighten-1\":  #d4e157,\n  \"darken-1\":   #c0ca33,\n  \"darken-2\":   #afb42b,\n  \"darken-3\":   #9e9d24,\n  \"darken-4\":   #827717,\n  \"accent-1\":    #f4ff81,\n  \"accent-2\":    #eeff41,\n  \"accent-3\":    #c6ff00,\n  \"accent-4\":    #aeea00\n);\n\n$yellow: (\n  \"base\":       #ffeb3b,\n  \"lighten-5\":  #fffde7,\n  \"lighten-4\":  #fff9c4,\n  \"lighten-3\":  #fff59d,\n  \"lighten-2\":  #fff176,\n  \"lighten-1\":  #ffee58,\n  \"darken-1\":   #fdd835,\n  \"darken-2\":   #fbc02d,\n  \"darken-3\":   #f9a825,\n  \"darken-4\":   #f57f17,\n  \"accent-1\":    #ffff8d,\n  \"accent-2\":    #ffff00,\n  \"accent-3\":    #ffea00,\n  \"accent-4\":    #ffd600\n);\n\n$amber: (\n  \"base\":       #ffc107,\n  \"lighten-5\":  #fff8e1,\n  \"lighten-4\":  #ffecb3,\n  \"lighten-3\":  #ffe082,\n  \"lighten-2\":  #ffd54f,\n  \"lighten-1\":  #ffca28,\n  \"darken-1\":   #ffb300,\n  \"darken-2\":   #ffa000,\n  \"darken-3\":   #ff8f00,\n  \"darken-4\":   #ff6f00,\n  \"accent-1\":    #ffe57f,\n  \"accent-2\":    #ffd740,\n  \"accent-3\":    #ffc400,\n  \"accent-4\":    #ffab00\n);\n\n$orange: (\n  \"base\":       #ff9800,\n  \"lighten-5\":  #fff3e0,\n  \"lighten-4\":  #ffe0b2,\n  \"lighten-3\":  #ffcc80,\n  \"lighten-2\":  #ffb74d,\n  \"lighten-1\":  #ffa726,\n  \"darken-1\":   #fb8c00,\n  \"darken-2\":   #f57c00,\n  \"darken-3\":   #ef6c00,\n  \"darken-4\":   #e65100,\n  \"accent-1\":    #ffd180,\n  \"accent-2\":    #ffab40,\n  \"accent-3\":    #ff9100,\n  \"accent-4\":    #ff6d00\n);\n\n$deep-orange: (\n  \"base\":       #ff5722,\n  \"lighten-5\":  #fbe9e7,\n  \"lighten-4\":  #ffccbc,\n  \"lighten-3\":  #ffab91,\n  \"lighten-2\":  #ff8a65,\n  \"lighten-1\":  #ff7043,\n  \"darken-1\":   #f4511e,\n  \"darken-2\":   #e64a19,\n  \"darken-3\":   #d84315,\n  \"darken-4\":   #bf360c,\n  \"accent-1\":    #ff9e80,\n  \"accent-2\":    #ff6e40,\n  \"accent-3\":    #ff3d00,\n  \"accent-4\":    #dd2c00\n);\n\n$brown: (\n  \"base\":       #795548,\n  \"lighten-5\":  #efebe9,\n  \"lighten-4\":  #d7ccc8,\n  \"lighten-3\":  #bcaaa4,\n  \"lighten-2\":  #a1887f,\n  \"lighten-1\":  #8d6e63,\n  \"darken-1\":   #6d4c41,\n  \"darken-2\":   #5d4037,\n  \"darken-3\":   #4e342e,\n  \"darken-4\":   #3e2723\n);\n\n$blue-grey: (\n  \"base\":       #607d8b,\n  \"lighten-5\":  #eceff1,\n  \"lighten-4\":  #cfd8dc,\n  \"lighten-3\":  #b0bec5,\n  \"lighten-2\":  #90a4ae,\n  \"lighten-1\":  #78909c,\n  \"darken-1\":   #546e7a,\n  \"darken-2\":   #455a64,\n  \"darken-3\":   #37474f,\n  \"darken-4\":   #263238\n);\n\n$grey: (\n  \"base\":       #9e9e9e,\n  \"lighten-5\":  #fafafa,\n  \"lighten-4\":  #f5f5f5,\n  \"lighten-3\":  #eeeeee,\n  \"lighten-2\":  #e0e0e0,\n  \"lighten-1\":  #bdbdbd,\n  \"darken-1\":   #757575,\n  \"darken-2\":   #616161,\n  \"darken-3\":   #424242,\n  \"darken-4\":   #212121\n);\n\n$shades: (\n  \"black\":        #000000,\n  \"white\":        #FFFFFF,\n  \"transparent\":  transparent\n);\n\n$colors: (\n  \"materialize-red\": $materialize-red,\n  \"red\": $red,\n  \"pink\": $pink,\n  \"purple\": $purple,\n  \"deep-purple\": $deep-purple,\n  \"indigo\": $indigo,\n  \"blue\": $blue,\n  \"light-blue\": $light-blue,\n  \"cyan\": $cyan,\n  \"teal\": $teal,\n  \"green\": $green,\n  \"light-green\": $light-green,\n  \"lime\": $lime,\n  \"yellow\": $yellow,\n  \"amber\": $amber,\n  \"orange\": $orange,\n  \"deep-orange\": $deep-orange,\n  \"brown\": $brown,\n  \"blue-grey\": $blue-grey,\n  \"grey\": $grey,\n  \"shades\": $shades\n);\n\n\n// Color Classes\n\n@each $color_name, $color in $colors {\n  @each $color_type, $color_value in $color {\n    @if $color_type == \"base\" {\n      .#{$color_name} {\n        background-color: $color_value !important;\n      }\n      .#{$color_name}-text {\n        color: $color_value !important;\n      }\n    }\n    @else if $color_name != \"shades\" {\n      .#{$color_name}.#{$color_type} {\n        background-color: $color_value !important;\n      }\n      .#{$color_name}-text.text-#{$color_type} {\n        color: $color_value !important;\n      }\n    }\n  }\n}\n\n// Shade classes\n@each $color, $color_value in $shades {\n  .#{$color} {\n    background-color: $color_value !important;\n  }\n  .#{$color}-text {\n    color: $color_value !important;\n  }\n}\n\n\n// usage: color(\"name_of_color\", \"type_of_color\")\n// to avoid to repeating map-get($colors, ...)\n\n@function color($color, $type) {\n  @if map-has-key($colors, $color) {\n    $curr_color: map-get($colors, $color);\n    @if map-has-key($curr_color, $type) {\n      @return map-get($curr_color, $type);\n    }\n  }\n  @warn \"Unknown `#{name}` in $colors.\";\n  @return null;\n}\n\n","// Susy Box Sizing\n// =================\n\n// Global Box Sizing\n// -----------------\n// Set a box model globally on all elements.\n// - [$box]: border-box | content-box\n// - [$inherit]: true | false\n@mixin global-box-sizing(\n  $box: susy-get(global-box-sizing),\n  $inherit: false\n) {\n  $inspect: $box;\n\n  @if $inherit {\n    @at-root {\n       html { @include output((box-sizing: $box)); }\n       *, *:before, *:after { box-sizing: inherit; }\n     }\n  } @else {\n    *, *:before, *:after { @include output((box-sizing: $box)); }\n  }\n\n  @include susy-inspect(global-box-sizing, $inspect);\n  @include update-box-model($box);\n}\n\n// Border Box Sizing\n// -----------------\n// A legacy shortcut...\n// - [$inherit]: true | false\n@mixin border-box-sizing(\n  $inherit: false\n) {\n  @include global-box-sizing(border-box, $inherit);\n}\n\n// Update Box Model\n// ----------------\n// PRIVATE: Updates global box model setting\n@mixin update-box-model(\n  $box\n) {\n  @if $box != susy-get(global-box-sizing) {\n  @include susy-set(global-box-sizing, $box);\n  }\n}\n","// rem Support\n// ===========\n\n// rem\n// ---\n// Check for an existing support mixin, or output directly.\n// - $prop  : <css property>\n// - $val   : <css value>\n@mixin susy-rem(\n  $prop,\n  $val\n) {\n  $_reqs: (\n    variable: rhythm-unit rem-with-px-fallback,\n    mixin: rem,\n  );\n  @if susy-support(rem, $_reqs, $warn: false) and $rhythm-unit == rem {\n    @include rem($prop, $val);\n  } @else {\n    #{$prop}: $val;\n  }\n}\n","//---------------------------------------------//\r\n// Susy config (2.2.12)\r\n// Docs - https://github.com/oddbird/susy\r\n//---------------------------------------------//\r\n\r\n@import '../../../assets/styles/appwide-metastyles.scss';\r\n\r\n$susy: (\r\n   columns: 24,\r\n   container: $hd-widescreen,\r\n   global-box-sizing: border-box,\r\n   gutters: 1/4, //<gutter-width>/<column-width> = // 20px gutters & 70px columns\r\n   gutter-position: after,\r\n   use-custom: (\r\n      clearfix: false,\r\n      background-image: true,\r\n      breakpoint: true,\r\n      box-sizing: false,\r\n   ),\r\n   debug: (\r\n      image: $susyDebug,\r\n      output: overlay, //background, overlay\r\n      toggle: bottom right,\r\n   ),\r\n);\r\n\r\n@include border-box-sizing; //uses border box sizing like the old IE\r\n\r\n.app-container {\r\n   @include container(100%);\r\n   padding: 0;\r\n}\r\n\r\n","// Container Syntax\n// ================\n\n// Container [mixin]\n// -----------------\n// Set a container element\n// - [$layout]  : <settings>\n@mixin container(\n  $layout: $susy\n) {\n  $inspect    : $layout;\n  $layout     : parse-grid($layout);\n\n  $_width     : get-container-width($layout);\n  $_justify   : parse-container-position(susy-get(container-position, $layout));\n  $_property  : if(susy-get(math, $layout) == static, width, max-width);\n\n  $_box       : susy-get(box-sizing, $layout);\n\n  @if $_box {\n    @include output((box-sizing: $_box));\n  }\n\n  @include susy-inspect(container, $inspect);\n  @include float-container($_width, $_justify, $_property);\n  @include show-grid($layout);\n}\n\n// Container [function]\n// --------------------\n// Return container width\n// - [$layout]  : <settings>\n@function container(\n  $layout: $susy\n) {\n  $layout: parse-grid($layout);\n  @return get-container-width($layout);\n}\n\n// Get Container Width\n// -------------------\n// Calculate the container width\n// - [$layout]: <settings>\n@function get-container-width(\n  $layout: $susy\n) {\n  $layout         : parse-grid($layout);\n  $_width         : susy-get(container, $layout);\n  $_column-width  : susy-get(column-width, $layout);\n  $_math          : susy-get(math, $layout);\n\n  @if not($_width) or $_width == auto {\n    @if valid-column-math($_math, $_column-width) {\n      $_columns   : susy-get(columns, $layout);\n      $_gutters   : susy-get(gutters, $layout);\n      $_spread    : if(is-split($layout), wide, narrow);\n      $_width     : susy-sum($_columns, $_gutters, $_spread) * $_column-width;\n    } @else {\n      $_width: 100%;\n    }\n  }\n\n  @return $_width;\n}\n\n// Parse Container Position\n// ------------------------\n// Parse the $container-position into margin values.\n// - [$justify]   : left | center | right | <length> [<length>]\n@function parse-container-position(\n  $justify: map-get($susy-defaults, container-position)\n) {\n  $_return: if($justify == left, 0, auto) if($justify == right, 0, auto);\n\n  @if not(index(left right center, $justify)) {\n    $_return: nth($justify, 1);\n    $_return: $_return if(length($justify) > 1, nth($justify, 2), $_return);\n  }\n\n  @return $_return;\n}\n","// Susy Fallback Clearfix\n// ======================\n\n\n// Clearfix\n// --------\n// Check for an existing support mixin, or provide a simple fallback.\n@mixin susy-clearfix {\n  @if susy-support(clearfix, (mixin: clearfix)) {\n    @include clearfix;\n  } @else {\n    &:after {\n      content: \" \";\n      display: block;\n      clear: both;\n    }\n  }\n}\n","// Background Grid Syntax\n// ======================\n\n$susy-overlay-grid-head-exists: false;\n\n\n// Show Grid/s\n// -----------\n// Show grid on any element using either background or overlay.\n// - [$grid]  : <settings>\n@mixin show-grid(\n  $grid: $susy\n) {\n  $inspect: $grid;\n  $_output: debug-get(output, $grid);\n\n  @include susy-inspect(show-grid, $inspect);\n  @if $_output == overlay and susy-get(debug image, $grid) != hide {\n    @include overlay-grid($grid);\n  } @else {\n    @include background-grid($grid);\n  }\n}\n\n@mixin show-grids(\n  $grid: $susy\n) {\n  @include show-grid($grid);\n}\n\n// Background Grid\n// ---------------\n// Show a grid background on any element.\n// - [$grid]  : <settings>\n@mixin background-grid(\n  $grid: $susy\n) {\n  $inspect  : $grid;\n  $_output  : get-background($grid);\n\n  @if length($_output) > 0 {\n    $_flow: susy-get(flow, $grid);\n\n    $_image: ();\n    @each $name, $layer in map-get($_output, image) {\n      $_direction: if($name == baseline, to bottom, to to($_flow));\n      $_image: append($_image, linear-gradient($_direction, $layer), comma);\n    }\n    $_output: map-merge($_output, (image: $_image));\n\n    @include background-grid-output($_output...);\n    @include susy-inspect(background-grid, $inspect);\n  }\n}\n\n\n// Overlay Grid\n// ------------\n// Generate an icon to trigger grid-overlays on any given elements.\n// $grids...  : <selector> [<settings>] [, <selector>]*\n@mixin overlay-grid (\n  $grid: $susy\n) {\n  @if not($susy-overlay-grid-head-exists) {\n    @at-root head { @include overlay-head($grid); }\n    @at-root head:before { @include overlay-trigger; }\n    @at-root head:hover { @include overlay-trigger-hover; }\n    $susy-overlay-grid-head-exists: true !global;\n  }\n\n  head:hover ~ &,\n  head:hover ~ body & {\n    position: relative;\n    &:before {\n      @include grid-overlay-base;\n      @include background-grid($grid);\n    }\n  }\n}\n\n\n// [Private] Overlay Trigger\n// -------------------------\n@mixin overlay-trigger {\n  content: \"|||\";\n  display: block;\n  padding: 5px 10px;\n  font: {\n    family: sans-serif;\n    size: 16px;\n    weight: bold;\n  }\n}\n\n\n// [Private] Overlay Trigger Hover\n// -------------------------------\n@mixin overlay-trigger-hover {\n  background: rgba(white, .5);\n  color: red;\n}\n\n\n// [Private] Overlay Head\n// ----------------------\n// <head> styles to create grid overlay toggle\n@mixin overlay-head (\n  $grid: $susy\n) {\n  $_toggle: debug-get(toggle, $grid);\n  $_horz: null;\n  $_vert: null;\n\n  @each $side in $_toggle {\n    $_horz: if($side == left or $side == right, $side, $_horz);\n    $_vert: if($side == top or $side == bottom, $side, $_vert);\n  }\n\n  display: block;\n  position: fixed;\n  #{$_horz}: 10px;\n  #{$_vert}: 10px;\n  z-index: 999;\n  color: #333;\n  background: rgba(white, .25);\n}\n\n\n// [Private] Grid Overlay Base\n// ---------------------------\n// Base styles for generating a grid overlay\n@mixin grid-overlay-base() {\n  position: absolute;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  content: \" \";\n  z-index: 998;\n}\n\n\n// Get Symmetrical Background\n// --------------------------\n// - $grid: <map>\n@function get-background-sym(\n  $grid\n) {\n  $grid           : parse-grid($grid);\n  $_gutters       : susy-get(gutters, $grid);\n  $_column-width  : susy-get(column-width, $grid);\n  $_math          : susy-g